import React, { ReactDOM } from 'react';
import { Link } from 'react-router-dom'
import { projectFirestore } from '../firebase';
import * as $ from "jquery";
import Slider from "react-slick";
import 'photoswipe/dist/photoswipe.css'
import 'photoswipe/dist/default-skin/default-skin.css'

import { Gallery, Item } from 'react-photoswipe-gallery'

class Index extends React.Component {
    constructor(props) {
        super(props);
        this.state.loading = true;
    }
    state = {
        loading: true,
        gallery: null,
        slider: null,
        company: null
    }
    async componentDidMount() {
        this.setState({ loading: false });
        await projectFirestore.collection('gallery')
            .onSnapshot(snap => {
                let documents = [];
                snap.forEach(doc => {
                    let j = { ...doc.data(), id: doc.id }
                    documents.push(j);
                });
                this.setState({ gallery: documents });
                //$('#bt-indexfourphotogallery').cubegallery('destroy');
            });
        await projectFirestore.collection('slider').orderBy("order")
            .onSnapshot(snap => {
                let documents = [];
                snap.forEach(doc => {
                    let j = { ...doc.data(), id: doc.id }
                    documents.push(j);
                });
                this.setState({ slider: documents });
            });
        await projectFirestore.collection('company')
            .onSnapshot(snap => {
                let documents = [];
                snap.forEach(doc => {
                    let j = { ...doc.data(), id: doc.id }
                    documents.push(j);
                });
                this.setState({ company: documents[0] });
            });
    }
    render() {
        const settings = {
            customPaging: function (i) {
                return (
                    <button type="button" data-role="none" role="button" >{i}</button>
                );
            },
            dots: true,
            dotsClass: "bt-slidernav",
            infinite: true,
        };

        return (
            <React.Fragment>
                {
                    this.state.loading ?
                        <div id="bt-preloader" >
                            <div className="bt-preloadericon" ></div>
                        </div> :
                        <React.Fragment>

                            <React.Fragment>
                                <nav id="bt-nav" className="bt-nav">
                                    <Link className="bt-btntogglemenu" to="#">X</Link>
                                    <div id="bt-navigation" className="bt-navigation">
                                        <strong className="bt-logo"><Link to="/index"><img src={`${process.env.PUBLIC_URL}/img/logo.png`}
                                            alt="image description" /></Link></strong>
                                        <ul className="bt-menu">
                                            <li className="bt-active bt-hasdropdown">
                                                <span>01.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/index`}>home</Link>
                                            </li>
                                            <li className="bt-hasdropdown">
                                                <span>02.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/about`}>about us</Link>
                                            </li>
                                            <li className="bt-hasdropdown">
                                                <span>03.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/gallery/1`}>Gallery</Link>
                                            </li>
                                            <li className="bt-hasdropdown">
                                                <span>04.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/contact`}>contact us</Link>
                                            </li>
                                        </ul>
                                        <ul className="bt-socialicons">
                                            <li><a href={this.state.company?.facebook} target="_blank" ><i className="fa fa-facebook"></i></a></li>
                                            <li><a href={this.state.company?.youtube} target="_blank"><i className="fa fa-youtube"></i></a></li>
                                            <li><a href={this.state.company?.instagram} target="_blank"><i className="fa fa-instagram"></i></a></li>
                                        </ul>
                                        <ul className="bt-navinfo">
                                            <li><span>Email </span><Link to={'mailto:' + this.state.company?.email}>{this.state.company?.email}</Link></li>
                                            <li><span>Phone {this.state.company?.mobile.toString()}</span></li>
                                            <li>
                                                <address>{this.state.company?.address}</address>
                                            </li>
                                        </ul>
                                    </div>
                                </nav>
                                <header id="bt-header" className="bt-header bt-headervfour bt-headerinnerpagev1 bt-haslayout">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                                <strong className="bt-logo"><Link to={`${process.env.PUBLIC_URL}/index`}><img src="./img/logo3.png"
                                                    style={{ margin: 'auto', height: '50px' }}
                                                    alt="image description" /></Link></strong>
                                                <div className="bt-navigationarea">
                                                    <ul>
                                                        <li className="bt-active bt-hasdropdown">
                                                            <span>01.</span>
                                                            <Link to="/index">home</Link>
                                                        </li>
                                                        <li className="bt-hasdropdown">
                                                            <span>02.</span>
                                                            <Link to="about">about us</Link>
                                                        </li>
                                                        <li className="bt-hasdropdown">
                                                            <span>03.</span>
                                                            <Link to="gallery/1">gallery</Link>
                                                        </li>
                                                        <li className="bt-hasdropdown">
                                                            <span>04.</span>
                                                            <Link to="contact">contact us</Link>
                                                        </li>
                                                        <li><Link className="bt-btntogglemenu" to="#"><img src="./img/icon-04.png"
                                                            alt="image description" /></Link></li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </header>
                            </React.Fragment>
                            <div className="bt-homeslider bt-haslayout">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                            <div id="bt-sliderfour" className="bt-sliderfour" role="toolbar">
                                                <Slider {...settings}>
                                                    {
                                                        this.state.slider && (
                                                            this.state.slider.map(d =>
                                                                <div key={d.id} className="bt-item" tabIndex="-1" role="option" aria-describedby="slick-slide00" >
                                                                    <div className="bt-imgbox">
                                                                        {d.img.map(i =>
                                                                            <figure key={i}><img src={'img/' + i} alt="image description" /></figure>)}

                                                                    </div>
                                                                    <div className="bt-contentbox">
                                                                        <div className="bt-postcontent">
                                                                            <ul className="bt-postmatadata">
                                                                                <li>
                                                                                    <div className="bt-authorimgname">
                                                                                        <a href="#" tabIndex="0"><img
                                                                                            src="img/img-01.jpg" alt="image description" /></a>
                                                                                        <span>by: <a href="#" tabIndex="0">{d.by}</a></span>
                                                                                    </div>
                                                                                </li>
                                                                                <li><time dateTime="2011-01-01">2017-07-12</time></li>
                                                                            </ul>
                                                                            <div className="bt-categoryposttitle">
                                                                                <h3>{d.title}</h3>
                                                                            </div>
                                                                            <div className="bt-description">
                                                                                <p>{d.desc}</p>
                                                                            </div>
                                                                            <Link className="bt-btn bt-btnblack" to={`${process.env.PUBLIC_URL}/gallery/1` + d.id}
                                                                                tabIndex="0"><span>explore</span></Link>
                                                                            <ul className="bt-shareandlikes">
                                                                                <li>
                                                                                    <div className="bt-shapreicons">
                                                                                        <a href="#" tabIndex="0"><i
                                                                                            className="fa fa-twitter"></i></a>
                                                                                        <a href="#" tabIndex="0"><i
                                                                                            className="fa fa-facebook"></i></a>
                                                                                        <a href="#" tabIndex="0"><i
                                                                                            className="fa fa-pinterest-p"></i></a>
                                                                                    </div>
                                                                                    <a className="bt-btnshare" href="#" tabIndex="0">
                                                                                        <span className="icon-share4"></span>
                                                                                        <span>Share</span>
                                                                                    </a>
                                                                                </li>
                                                                                <li>
                                                                                    <a href="#" tabIndex="0">
                                                                                        <span className="icon-heart12"></span>
                                                                                        <span>{d.likes}</span>
                                                                                    </a>
                                                                                </li>
                                                                            </ul>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            )
                                                        )
                                                    }
                                                </Slider>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <main id="bt-main" className="bt-main bt-haslayout">
                                <section className="bt-sectionspace bt-bglight bt-haslayout">
                                    <div className="container">
                                        <div className="row">
                                            <div className="bt-aboutus">
                                                <div className="bt-abouttextbox bt-verticalmiddle">
                                                    <h2>We don't follow trends<span>We create them</span></h2>
                                                    <div className="bt-description">
                                                        <p>It is a long established fact that a reader will be distracted by the readable
                                                        content of a Many desktop publishing packages and web page editors now use Lorem
                                                        Ipsum as their default model text, and a search for 'lorem ipsum' will uncover
                                                        many web sites still in their infancy. Various versions have evolved over the
										years</p>
                                                    </div>
                                                    <a className="bt-btn bt-btnblack" href="contact"><span>read more</span></a>
                                                </div>
                                                <figure className="bt-aboutimgbox bt-verticalmiddle hidden-xs"><a
                                                    href="contact"><img src="./img/p1.jpg"
                                                        alt="image description" /></a></figure>
                                            </div>
                                        </div>
                                    </div>
                                </section>
                                <div className="bt-sectionspace bt-haslayout">
                                    <div className="container">
                                        <div className="row">
                                            <div className="bt-photogallery">
                                                <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                                    <div className="bt-pageheadcontent">
                                                        <h2>Photo Gallery</h2>
                                                        <div className="bt-description">
                                                            <p>It is a long established fact that a reader will be distracted by the
											readable content of a page when looking at its layout</p>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12" id="smallGallery">
                                                    <Gallery>
                                                        {
                                                            this.state.gallery &&
                                                            this.state.gallery.map(d =>
                                                                d.img.map(i =>
                                                                    <Item key={i}
                                                                        original={'img/' + i}
                                                                        thumbnail={'img/' + i}
                                                                        width="1024"
                                                                        height="768"
                                                                    >
                                                                        {({ ref, open }) => (
                                                                            <img ref={ref} onClick={open} src={'img/' + i} />
                                                                        )}
                                                                    </Item>
                                                                )
                                                            )
                                                        }

                                                    </Gallery>
                                                </div>
                                                <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12" style={{ display: 'none' }}>
                                                    <div id="bt-indexfourphotogallery"
                                                        className="bt-indexfourphotogallery ">
                                                        {
                                                            false &&
                                                            this.state.gallery.map(d =>
                                                                <div key={d.id} className="cbp-item" >
                                                                    <article className="bt-album">
                                                                        <figure>

                                                                            {/* {
                                                                    d.img.map(i =>
                                                                        <a key={i} href={'img/' + i} data-rel="prettyPhoto[kids]"
                                                                            rel="prettyPhoto[kids]">
                                                                            <img src={'img/' + i}
                                                                                alt="Kids Special Album" /></a>)
                                                                } */}
                                                                            <figcaption>

                                                                                <div className="bt-titleandinfo">
                                                                                    <div className="bt-albumtitle">
                                                                                        <h3><a href="#">{d.type}
                                                                                        </a></h3>
                                                                                    </div>
                                                                                    <ul className="bt-shareandlikes">
                                                                                        <li>
                                                                                            <div className="bt-shapreicons">
                                                                                                <a href="#"><i
                                                                                                    className="fa fa-twitter"></i></a>
                                                                                                <a href="#"><i
                                                                                                    className="fa fa-facebook"></i></a>
                                                                                                <a href="#"><i
                                                                                                    className="fa fa-pinterest-p"></i></a>
                                                                                            </div>
                                                                                            <a className="bt-btnshare"
                                                                                                href="#">
                                                                                                <span className="icon-share4"></span>
                                                                                                <span>Share</span>
                                                                                            </a>
                                                                                        </li>
                                                                                        <li>
                                                                                            <a href="#">
                                                                                                <span className="icon-heart12"></span>
                                                                                                <span>23</span>
                                                                                            </a>
                                                                                        </li>
                                                                                    </ul>
                                                                                </div>
                                                                            </figcaption>
                                                                        </figure>
                                                                    </article>
                                                                </div>
                                                            )
                                                        }
                                                    </div>

                                                </div>
                                                <div id="bt-loadMore" className="bt-btnboxloadmore bt-sectionspace cbp-l-loadMore-button">
                                                    <a href="gallery" className="cbp-l-loadMore-link" rel="nofollow">
                                                        <span className="cbp-l-loadMore-defaultText">LOAD MORE <span
                                                            className="cbp-l-loadMore-loadItems"></span></span>
                                                        <span className="cbp-l-loadMore-loadingText">LOADING...</span>
                                                        <span className="cbp-l-loadMore-noMoreLoading">NO MORE WORKS</span>
                                                    </a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </main>
                        </React.Fragment >
                }
            </React.Fragment>
        );
    }
    useFirestore(collection) {
        return
    }
}




export default Index;
