import React from 'react';
import { Link } from 'react-router-dom';

class Contact extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            compony: this.props.comp
        }
    }
    render() {
        return (
            <React.Fragment>

                <React.Fragment>
                    <nav id="bt-nav" className="bt-nav" style={{ display: 'none' }}>
                        <Link to="#" className="bt-btntogglemenu" to="#">X</Link>
                        <div id="bt-navigation" className="bt-navigation">
                            <strong className="bt-logo"><Link to="index"><img src="img/logo3.png"
                                alt="image description" /></Link></strong>
                            <ul className="bt-menu">
                                <li className="bt-hasdropdown">
                                    <span>01.</span>
                                    <Link to={`${process.env.PUBLIC_URL}/index`}>home</Link>
                                </li>
                                <li className="bt-hasdropdown">
                                    <span>02.</span>
                                    <Link to={`${process.env.PUBLIC_URL}/about`}>about us</Link>
                                </li>
                                <li className="bt-hasdropdown">
                                    <span>03.</span>
                                    <Link to={`${process.env.PUBLIC_URL}/gallery/1`}>Gallery</Link>
                                </li>
                                <li className="bt-active bt-hasdropdown">
                                    <span>04.</span>
                                    <Link to={`${process.env.PUBLIC_URL}/contact`}>Contact Us</Link>
                                </li>

                            </ul>
                            <ul className="bt-socialicons">
                                <li><a href={this.props.comp?.facebook} target="_blank" ><i className="fa fa-facebook"></i></a></li>
                                <li><a href={this.props.comp?.youtube} target="_blank"><i className="fa fa-youtube"></i></a></li>
                                <li><a href={this.props.comp?.instagram} target="_blank"><i className="fa fa-instagram"></i></a></li>
                            </ul>
                            <ul className="bt-navinfo">
                                <li><span>Email </span><Link to={'mailto:' + this.props.comp?.email}>{this.props.comp?.email}</Link></li>
                                <li><span>Phone {this.props.comp?.mobile.toString()}</span></li>
                                <li>
                                    <address>{this.props.comp?.address}</address>
                                </li>
                            </ul>
                        </div>
                    </nav>

                    <header id="bt-header" className="bt-header bt-headervfour bt-haslayout">
                        <div className="container">
                            <div className="row">
                                <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                    <strong className="bt-logo"><Link to="#"><img src={`img/logo3.png`} alt="image description" style={{
                                        height: '50px',
                                        margin: 'auto'
                                    }} /></Link></strong>
                                    <div className="bt-navigationarea">
                                        <ul>
                                            <li className="bt-hasdropdown">
                                                <span>01.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/index`}>home</Link>
                                            </li>
                                            <li className="bt-hasdropdown">
                                                <span>02.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/about`}>about us</Link>

                                            </li>
                                            <li className=" bt-hasdropdown">
                                                <span>03.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/gallery/1`}>gallery</Link>

                                            </li>
                                            <li className="bt-active bt-hasdropdown">
                                                <span>04.</span>
                                                <Link to={`${process.env.PUBLIC_URL}/contact`}>contact us</Link>
                                            </li>
                                            <li><Link to="#" className="bt-btntogglemenu"
                                                style={{
                                                    filter: 'invert(1)'
                                                }}
                                            ><img src={`img/icon-01.png`}
                                                alt="image description" /></Link></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </header>
                    <div className="bt-innerpagebanner bt-innerpagebannerv2 bt-fullheight" style={{ height: '600px' }}>

                        <figure className="bt-fullheight" data-vide-bg="poster: img/bgcontactv2"
                            data-vide-options="position: 0% 50%">
                            <div
                                style={{
                                    position: 'absolute', zIndex: '-1', top: '0px', left: '0px', bottom: '0px',
                                    right: '0px', overflow: 'hidden', backgroundImage: 'url("img/img-06.jpg")',
                                    backgroundSize: 'cover', backgroundColor: 'transparent', backgroundRepeat: 'no-repeat', backgroundPosition: '0% 50%'
                                }}>
                                <video autoPlay="" loop="" muted=""
                                    style={{
                                        margin: 'auto', position: 'absolute',
                                        zIndex: -1,
                                        top: '50%',
                                        left: '0%',
                                        transform: 'translate(0%, -50%)',
                                        visibility: 'hidden', opacity: 0, width: 'auto', height: '713px'
                                    }}></video>
                            </div>

                            <figcaption>
                                <div className="container">
                                    <div className="row">
                                        <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                            <div className="bt-innerbannercontent">
                                                <div className="bt-borderheading">
                                                    <h1>Feel free to get in touch with us to set up<span>a time for a casual
                                                        meeting</span></h1>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </figcaption>
                            <Link id="bt-btnscrollto" className="bt-btnscrollto bt-btnscroll" to="#bt-main">scroll</Link>
                        </figure>
                    </div>
                </React.Fragment>


                <main id="bt-main" className="bt-main bt-haslayout">
                    <div className="container">
                        <div className="row">
                            <div id="bt-content" className="bt-content">
                                <div className="bt-contactus bt-contactusvtwo">
                                    <ul className="bt-contactinfo">
                                        <li>
                                            <i><img src="img/icon-08.png" alt="image description" /></i>
                                            {this.props.comp?.mobile
                                                && this.props.comp?.mobile.map(i =>
                                                    <span>{i}</span>
                                                )

                                            }
                                        </li>
                                        <li>
                                            <i><img src="img/icon-09.png" alt="image description" /></i>
                                            <address>{this.props.comp?.address}</address>
                                        </li>
                                        <li>
                                            <i><img src="img/icon-10.png" alt="image description" /> </i>
                                            <span><a href={'mailto:' + this.props.comp?.email} target="_blank">{this.props.comp?.email}</a></span>

                                        </li>
                                    </ul>
                                    <div className="bt-btnbox">
                                        <Link className="bt-btn bt-btnblack" to="#"><span>get direction</span></Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </main>
            </React.Fragment>

        );
    }
}

export default Contact;
