import React from 'react';
import { Link } from 'react-router-dom';
import 'photoswipe/dist/photoswipe.css';
import 'photoswipe/dist/default-skin/default-skin.css';
import { Gallery as Gal, Item } from 'react-photoswipe-gallery';
import MyContext from '../context/mycontext';
class Gallery extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            compony: this.props.comp,
            gallery: [],
            id: (this.props?.match?.params.id == '1' ? 'lL6bmMGuEHCnxT2J1VbT' : this.props?.match?.params.id) || 'lL6bmMGuEHCnxT2J1VbT',
            index: 0,
            prev: 0,
            next: 0
        }
        this.handleButtonChange = this.handleButtonChange.bind(this);
    }
    async componentDidMount() {
        this.setState({ loading: false });
    }

    handleButtonChange(v) {
        this.setState({ id: v })
        const index = this.state.gallery.findIndex(n => n.sliderid == this.state.id);
        let l = this.state.gallery.length||0;
        const prev = index == 0 ? l - 1 : index - 1;
        const next = index == l ? 0 : index + 1;
        this.setState({ index, prev, next })
    }
    render() {
        return (
            <MyContext.Consumer>
                {(context) => {
                    const gal = this.state.gallery;
                    if (gal.length == 0 && Object.keys(context.state.gallery).length) {
                        this.setState({ gallery: context.state.gallery })
                        const index = context.state.gallery.findIndex(n => n.sliderid == this.state.id)
                        let l = context.state.gallery.length
                        const prev = index == 0 ? l - 1 : index - 1;
                        const next = index == l ? 0 : index + 1;
                        this.setState({ index, prev, next })
                    }
                    return <React.Fragment>
                        <nav id="bt-nav" className="bt-nav" style={{ display: 'none' }}>
                            <Link className="bt-btntogglemenu" to="#">X</Link>
                            <div id="bt-navigation" className="bt-navigation">
                                <strong className="bt-logo"><Link to="index"><img src="img/logo3.png"
                                    alt="image description" /></Link></strong>
                                <ul className="bt-menu">
                                    <li className="bt-hasdropdown">
                                        <span>01.</span>
                                        <Link to={`${process.env.PUBLIC_URL}/index`}>home</Link>
                                    </li>
                                    <li className="bt-hasdropdown">
                                        <span>02.</span>
                                        <Link to={`${process.env.PUBLIC_URL}/about`}>about us</Link>
                                    </li>
                                    <li className="bt-active bt-hasdropdown">
                                        <span>03.</span>
                                        <Link to={`${process.env.PUBLIC_URL}/gallery/1`}>Gallery</Link>
                                    </li>
                                    <li className="bt-hasdropdown">
                                        <span>04.</span>
                                        <Link to={`${process.env.PUBLIC_URL}/contact`}>Contact Us</Link>
                                    </li>

                                </ul>
                                <ul className="bt-socialicons">
                                    <li><a href={this.props.comp?.facebook} target="_blank" ><i className="fa fa-facebook"></i></a></li>
                                    <li><a href={this.props.comp?.youtube} target="_blank"><i className="fa fa-youtube"></i></a></li>
                                    <li><a href={this.props.comp?.instagram} target="_blank"><i className="fa fa-instagram"></i></a></li>
                                </ul>
                                <ul className="bt-navinfo">
                                    <li><span>Email </span><Link to={'mailto:' + this.props.comp?.email}>{this.props.comp?.email}</Link></li>
                                    <li><span>Phone {this.props.comp?.mobile.toString()}</span></li>
                                    <li>
                                        <address>{this.props.comp?.address}</address>
                                    </li>
                                </ul>
                            </div>
                        </nav>

                        <header id="bt-header" className="bt-header bt-headervfour bt-haslayout">
                            <div className="container">
                                <div className="row">
                                    <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                        <strong className="bt-logo"><Link to="#"><img src={`img/logo3.png`} alt="image description" style={{
                                            height: '50px',
                                            margin: 'auto'
                                        }} /></Link></strong>
                                        <div className="bt-navigationarea">
                                            <ul>
                                                <li className="bt-hasdropdown">
                                                    <span>01.</span>
                                                    <Link to={`${process.env.PUBLIC_URL}/index`}>home</Link>
                                                </li>
                                                <li className="bt-hasdropdown">
                                                    <span>02.</span>
                                                    <Link to={`${process.env.PUBLIC_URL}/about`}>about us</Link>

                                                </li>
                                                <li className="bt-active  bt-hasdropdown">
                                                    <span>03.</span>
                                                    <Link to={`${process.env.PUBLIC_URL}/gallery/1`}>gallery</Link>

                                                </li>
                                                <li className="bt-hasdropdown">
                                                    <span>04.</span>
                                                    <Link to={`${process.env.PUBLIC_URL}/contact`}>contact us</Link>
                                                </li>
                                                <li><Link to="#" className="bt-btntogglemenu"
                                                    style={{
                                                        filter: 'invert(1)'
                                                    }}
                                                ><img src={`img/icon-01.png`}
                                                    alt="image description" /></Link></li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </header>

                        <main id="bt-main" className="bt-main bt-haslayout">
                            <div className="container">
                                <div className="row">
                                    <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                                        <div id="bt-content" className="bt-content">
                                            {this.state.gallery &&
                                                <div className="bt-portfolio bt-portfoliovone">
                                                    <figure className="bt-featuredimg">
                                                        {
                                                            context.state.gallery && (
                                                                <img src={`img/` + context.state.gallery[this.state.index]?.img[2]} alt="image description" />
                                                            )
                                                        }
                                                        <figcaption>
                                                            <ul className="bt-postmatadata">
                                                                <li>
                                                                    <div className="bt-authorimgname">
                                                                        <Link to="#" tabIndex="0"><img src={`img/img-01(1).jpg`} alt="image description" /></Link>
                                                                        <span>by: <Link to="#" tabIndex="0">Joshi Anand</Link></span>
                                                                    </div>
                                                                </li>
                                                                <li><time dateTime="2011-01-01">2017 - 07 - 12</time></li>
                                                            </ul>
                                                        </figcaption>
                                                    </figure>
                                                    <div className="bt-portfolioinfo">
                                                        <ul className="bt-clientinfo">
                                                            <li>
                                                                <span>Client:</span>
                                                                <strong>****</strong>
                                                            </li>
                                                            <li>
                                                                <span>Client type:</span>
                                                                <strong>Beauty / Modal</strong>
                                                            </li>
                                                        </ul>
                                                        <ul className="bt-shareandlikes">
                                                            <li>
                                                                <div className="bt-shapreicons">
                                                                    <Link to="#"><i className="fa fa-twitter"></i></Link>
                                                                    <Link to="#"><i className="fa fa-facebook"></i></Link>
                                                                    <Link to="#"><i className="fa fa-pinterest-p"></i></Link>
                                                                </div>
                                                                <Link className="bt-btnshare" to="#">
                                                                    <span className="icon-share4"></span>
                                                                    <span>Share</span>
                                                                </Link>
                                                            </li>
                                                            <li>
                                                                <Link to="#">
                                                                    <span className="icon-heart12"></span>
                                                                    <span>23</span>
                                                                </Link>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                    <div className="bt-portfoliotextbox">
                                                        <div className="bt-textbox">
                                                            <h2>{
                                                                context.state.gallery &&
                                                                context.state.gallery[this.state.index]?.type
                                                            }</h2>
                                                            <div className="bt-description">
                                                                <p>It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout.</p>
                                                            </div>
                                                        </div>
                                                        <div className="bt-textbox">
                                                            <div className="bt-description">
                                                                <p> </p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <div id="bt-portfoliovonegallery" className="bt-portfoliogallery " >
                                                        <Gal>
                                                            {
                                                                context.state.gallery &&
                                                                context.state.gallery[this.state.index]?.img.map(i =>
                                                                    <Item key={i}
                                                                        original={'img/' + i}
                                                                        thumbnail={'img/' + i}
                                                                        width="1024"
                                                                        height="768"
                                                                        key={i}
                                                                    >
                                                                        {({ ref, open }) => (
                                                                            <img ref={ref} onClick={open} src={`img/` + i} />
                                                                        )}
                                                                    </Item>
                                                                )
                                                            }

                                                        </Gal>
                                                    </div>
                                                    
                                                    <ul className="bt-postnav" >
                                                        <li>
                                                            <h3><label onClick={()=>this.handleButtonChange((context.state.gallery[this.state.prev]?.sliderid))} >{context.state.gallery && context.state.gallery[this.state.prev]?.type}</label></h3>
                                                            <label onClick={()=>this.handleButtonChange((context.state.gallery[this.state.prev]?.sliderid))} >
                                                                <i className="icon-arrow-left22"></i>
                                                                <span>Previous Post</span>
                                                            </label>
                                                        </li>



                                                        <li>
                                                        <h3><label onClick={()=>this.handleButtonChange((context.state.gallery[this.state.next]?.sliderid))} >{context.state.gallery && context.state.gallery[this.state.next]?.type}</label></h3>
                                                            <label onClick={()=>this.handleButtonChange((context.state.gallery[this.state.next]?.sliderid))}>
                                                                <span>next Post</span>
                                                                <i className="icon-arrow-right22"></i>
                                                            </label>
                                                        </li>
                                                    </ul>
                                                </div>
                                            }
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </main>

                    </React.Fragment >

                }}
            </MyContext.Consumer>
        );
    }
}

export default Gallery;
