import './App.css';
import React from 'react';
import { HashRouter as Router, Switch, Route, Link } from 'react-router-dom';

import Index from './components/index';
import Contact from './components/contact';
import Gallery from './components/gallery';
import About from './components/about';
import * as $ from "jquery";
import { projectFirestore } from './firebase';
import MyContext  from './context/mycontext';
import GalleryDetail from './components/gallery_detail';

class App extends React.Component {
  state = {
    loading: true,
    company: null,
    data: {},
    gallery: {}
  }
  toggle = (e) => {
    console.log(e)
  }
  async componentDidMount() {
    try {
      if (!localStorage.getItem('company')) {
        !this.state.company &&
          projectFirestore.collection('company').get()
            .then(snap => {
              let documents = [];
              snap.forEach(doc => {
                let j = { ...doc.data(), id: doc.id }
                documents.push(j);
              });
              this.setState({ company: documents[0] });
              localStorage.setItem('company', JSON.stringify(documents[0]))
            });
      } else {
        this.setState({ company: JSON.parse(localStorage.getItem('company')) })
      }
      if (!localStorage.getItem('gallery')) {
        projectFirestore.collection('gallery').get()
          .then(docSnapshot => {
            let documents = [];
            docSnapshot.forEach(doc => {
              let j = { ...doc.data(), id: doc.id }
              documents.push(j);
            });
            this.setState({ gallery: documents });
            localStorage.setItem('gallery', JSON.stringify(documents))
          }, err => {
            console.log(`error: ${err}`);
          });
      } else {
        this.setState({ gallery: JSON.parse(localStorage.getItem('gallery')) })
      }
    } catch (e) {
      console.log('error', e);
    }
  }
  render() {
    return (
      <MyContext.Provider value={{ state: this.state, toggle: this.toggle }}>
        <Router>
          <Switch >
            <Route
              exact
              path="/"
              render={() => (<Index comp={this.state.company} />)} />
            <Route path="/index" render={() => (<Index comp={this.state.company} />)} />
            <Route path="/about" render={() => (<About comp={this.state.company} />)} />
            <Route path="/contact" render={() => (<Contact comp={this.state.company} />)} />
            <Route path="/gallery/:id" render={(props) => (<Gallery {...props} />)} />
            <Route path="/details" render={(props) => (<GalleryDetail {...props} />)} />
          </Switch>
          <React.Fragment>
            <footer id="bt-footer" className="bt-footer bt-haslayout">
              <div className="container">
                <div className="row">
                  <div className="col-xs-12 col-sm-12 col-md-12 col-lg-12">
                    <div className="bt-newsletter">
                      <div className="col-xs-12 col-sm-7 col-md-7 col-lg-7">
                        <h3>Newsletter</h3>
                        <div className="bt-description">
                          <p>Get alert for latest offers from Joshi Studios</p>
                        </div>
                      </div>
                      <div className="col-xs-12 col-sm-5 col-md-5 col-lg-5">
                        <form className="bt-formtheme bt-formnewsletter">
                          <fieldset>
                            <input type="email" name="email" className="form-control"
                              placeholder="Enter your E-mail" />
                            <button type="submit"><i className="icon-email2"></i></button>
                          </fieldset>
                        </form>
                      </div>
                    </div>
                    <div className="bt-instagramgallery">
                      <ul>
                        <li>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-01(9).jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-01(9).jpg"
                              alt="image description" /></a></figure>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-01(4).jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-01(4).jpg"
                              alt="image description" /></a></figure>
                        </li>
                        <li>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-02(7).jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-02(7).jpg"
                              alt="image description" /></a></figure>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-06(1).jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-06(1).jpg"
                              alt="image description" /></a></figure>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-08.jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-08.jpg"
                              alt="image description" /></a></figure>
                        </li>
                        <li>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-03(7).jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-03(7).jpg"
                              alt="image description" /></a></figure>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-07.jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-07.jpg"
                              alt="image description" /></a></figure>
                        </li>
                        <li>
                          <figure><a href={`${process.env.PUBLIC_URL}/css/img-04(7).jpg`} data-rel="prettyPhoto[instagramphotos]"
                            rel="prettyPhoto[instagramphotos]"><img src="img/img-04(7).jpg"
                              alt="image description" /></a></figure>
                        </li>
                      </ul>
                      <div className="bt-instagrambox">
                        <i><img src={`img/icon-02.png`} alt="" /></i>
                        <span>@joshi_studios</span>
                        <a className="bt-btn bt-btnblack bt-btn-lg" href="https://www.instagram.com/joshi_studios/" target="_blank"><span>follow
										now</span></a>
                      </div>
                    </div>
                    <div className="bt-footerbar">
                      <div className="bt-navandcopyright">
                        <nav className="bt-addnav">
                          <ul>
                            <li><a href="#">Home</a></li>
                            <li><a href="#">FAQ</a></li>
                            <li><a href="#">TERMS OF SERVICES</a></li>
                            <li><a href="#">PRIVACY &amp; COOKIES</a></li>
                            <li><Link to={`${process.env.PUBLIC_URL}/contact`}>CONTACT Us</Link></li>
                          </ul>
                        </nav>
                        <span className="bt-copyright">Copyrights 2021 Joshi Studios. All rights reserved</span>
                      </div>
                      <div className="bt-followus">
                        <span>follow us</span>
                        <ul className="bt-socialicons">
                          <li><a href={'http://wa.me/919044293732'} target="_blank"><i className="fa fa-whatsapp"></i></a></li>
                          <li><a href={this.state.company?.instagram} target="_blank"><i className="fa fa-instagram"></i></a></li>
                          <li><a href={this.state.company?.youtube} target="_blank"><i className="fa fa-youtube"></i></a></li>
                          <li><a href={this.state.company?.facebook} target="_blank"><i className="fa fa-facebook"></i></a></li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </footer>
          </React.Fragment>
        </Router>
      </MyContext.Provider>
    );
  }
}

function getPhoto(a) {
  var regex = new RegExp(/^(?!.*\.\.)(?!.*\.$)[^\W][\w.]{0,29}$/);
  var validation = regex.test(a);
  if (validation) {
    $.get("https://www.instagram.com/" + a + "/?__a=1")
      .done(function (data) {
        return data.graphql?.user;
      })
      .fail(function () {
        return {}
      })

  } else {
    return {}
  }

}

export default App;
